.level-bar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  padding-top: 15px;
  padding-right: 17px;
  padding-bottom: 9px;
  padding-left: 17px;

  color: @basic-white;

  background-color: @special-hover-black;
  border-radius: 50%;

  &--catalog {
    background-color: inherit;

    .level-bar__text {
      font-size: @additional-text-font-size;
      line-height: @additional-text-line-height;
      color: @alt-text-color;

      @media (min-width: @tablet-width) {
        display: none;
      }
    }

    .level-bar__level {
      color: @special-blue;
    }

    .level-bar__text-eng {
      display: none;

      @media (min-width: @tablet-width) {
        display: block;

        color: @special-level-black;
      }
    }
  }

  &--form {
    background-color: inherit;

    @media (min-width: @desktop-width) {
      margin-right: 44px;
      margin-bottom: 14px;
      padding-top: 23px;
      padding-right: 28px;
      padding-bottom: 15px;
    }

    .level-bar__level {
      font-size: @level-form-font-size;
      line-height: @level-form-line-height;
      color: @basic-dark-blue;

      @media (min-width: @desktop-width) {

        color: @basic-white;
      }
    }

    .level-bar__text {
      font-size: @roadmap-font-size;
      line-height: @roadmap-line-height;
      color: @alt-text-color;

      @media (min-width: @desktop-width) {
        margin-left: 5px;

        color: @basic-white;
      }
    }
  }
}

.level-bar__level {
  margin-bottom: 5px;

  font-weight: @font-bold;
  font-size: @heading-font-size;
  line-height: @heading-line-height;
}

.level-bar__text {
  font-size: @details-font-size;
  line-height: @details-line-height;
}
