}
.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Blogger Sans", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  background-color: #EDEFF6;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
