}
.header--opened {
  background-color: #FFFFFF;
}
.header--opened .header__nav {
  display: block;
}
.header--opened .header__center {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  background-color: #FFFFFF;
}
.header--opened .header__nav-toggle {
  width: 18px;
  height: 19px;
}
.header--opened .header__nav-toggle::before,
.header--opened .header__nav-toggle::after {
  content: "";
  position: absolute;
  top: 8px;
  right: -2px;
  width: 24px;
  height: 2px;
  background-color: #CCCCCC;
}
.header--opened .header__nav-toggle::before {
  transform: rotate(45deg);
}
.header--opened .header__nav-toggle::after {
  transform: rotate(-45deg);
}
.header--opened .header__nav-toggle:hover::before,
.header--opened .header__nav-toggle:hover::after {
  background-color: #999999;
}
.header--opened .header__nav-toggle:focus::before,
.header--opened .header__nav-toggle:focus::after {
  background-color: #999999;
}
.header--opened .header__nav-toggle:active::before,
.header--opened .header__nav-toggle:active::after {
  background-color: #E5E5E5;
}
.header--opened .header__logo--light {
  display: none;
}
.header--opened .header__logo--dark {
  display: block;
}
.header--closed .header__nav-toggle {
  width: 25px;
  height: 11px;
}
.header--closed .header__nav-toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 3px;
  background-color: #FFFFFF;
  border-radius: 1.5px;
  box-shadow: 0 8px 0 0 #FFFFFF;
}
.header--closed .header__nav-toggle:hover::before {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.6);
}
.header--closed .header__nav-toggle:focus::before {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.6);
}
.header--closed .header__nav-toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3);
}
